<template>
    <div class="exchangeBox">
        <div class="exchange-box" v-if="couponList.exchange == 1">
            <div class="main">
                <div class="left-box">
                    <div class="coupon-style-one" v-if="type == 0 || type == 1">
                        <div class="top">
                            <span class="unit">￥</span>
                            <span class="price" v-if="isintegral==0">{{ keepOneNum(couponList.couponsPrice) }}</span>
                            <span class="price" v-else>{{ keepOneNum(couponList.price) }}</span>
                            <span class="type">{{ $L('代金券') }}</span>
                        </div>
                        <div class="bottom">
                            <div class="condtion" v-if="couponList.title"><span>{{ $L('使用条件') }}：</span>{{ couponList.title}}</div>
                            <div class="condtion" v-else><span>{{ $L('使用条件') }}：</span>{{ $L('全部商品可用') }}</div>
                            <div class="time" v-if="isintegral==0"><span>{{ $L('有效时间') }}：</span>{{ couponList.startTime }} - {{ couponList.endTime }}</div>
                            <div class="time" v-else>
                                <span v-if="couponList.money != 0">{{ couponList.money }}{{$L('元')}}</span>
                                <span v-if="couponList.money !=0 && couponList.integral != 0"> + </span>
                                <span v-if="couponList.integral != 0">{{ couponList.integral }}{{$L('分')}}</span>
                            </div>
                        </div>
                    </div>
                    <div class="img-box" v-else-if="type == 2 || type == 3">
                        <img src="../../../assets/img/prize/prize01.png" alt="">
                    </div>
                </div>
                <div class="right-box">
                    <p v-if="type == 0 || type == 1">满499减60代金券</p>
                    <p v-else-if="type == 2 || type == 3">兑换礼品名称</p>
                    <table class="table">
                        <tr v-if="type == 2 || type == 3">
                            <td>礼品价格：</td>
                            <td>￥59.00</td>
                        </tr>
                        <tr>
                            <td>兑换价：</td>
                            <td>￥0.00+1000积分</td>
                        </tr>
                        <tr>
                            <td>限兑：</td>
                            <td>1 <span>已兑/总量：2/100</span></td>
                        </tr>
                        <tr>
                            <td>兑换说明：</td>
                            <td></td>
                        </tr>
                        <tr>
                            <td>兑换时间：</td>
                            <td>2019-02-22 00:00:00~2019-03-22 23:59:59</td>
                        </tr>
                        <tr>
                            <td>兑换数量：</td>
                            <td><el-input-number size="small" v-model="num" @change="handleChange" :min="1" :max="99" label="描述文字"></el-input-number></td>
                        </tr>
                    </table>
                </div>
            </div>
            <div class="bottoms">
                <span>当前积分:114</span>
                <el-button>{{ $L('赚积分') }}</el-button>
                <el-button type="primary" @click="exchangeFunc">{{ $L('立即兑换') }}</el-button>
            </div>
        </div>
        <div class="submit-box" v-else-if="couponList.exchange == 0">
            <div class="success" v-if="couponList.success == 1">
                <i class="bbx-font bbx-icon-chenggong"></i>
                <p>兑换成功~！赶紧去使用吧~！</p>
            </div>
            <div class="fail" v-if="couponList.success == 0">
                <i class="bbx-font bbx-icon-shibai"></i>
                <p>兑换失败~！请再接再厉~！</p>
            </div>
        </div>

    </div>
</template>

<script>
    export default {
        name: "exchangeBox",
        components:{

        },
        data(){
            return {
                isintegral: 0,
                type: 2,
                num: 1,
                couponList:{
                    exchange:1,
                    success:1,
                    cardtype:'CASH',
                    type:'CASH',
                    couponsPrice:'120',
                    title:'满120元使用',
                    price:'500',
                    startTime:'2019.01.01',
                    endTime:'2019.08.01',
                    money:'888',
                    integral:'1'
                }
            }
        },
        methods:{
            // 保留小数位数
            keepOneNum(val){
                let num = Number(val);
                return num.toFixed(0);
            },
            handleChange(value){
                console.log(value);
            },
            exchangeFunc(){
                this.couponList.exchange = 0;
            }
        },
    }
</script>

<style lang="scss" scoped>
    @import "../../../assets/icon/iconfont.css";
    .exchangeBox{
        width: 700px;
        margin: 50px;
        height: 405px;
        position: relative;
        .main{
            padding: 20px;
            overflow: hidden;
            .left-box{
                float: left;
                padding: 10px;
                width: 40%;
                .coupon-style-one{
                    height: 170px;
                    color: #fff;
                    background: url("../../../assets/img/distributor/coupon-bg.png");
                    background-size: 100% 100%;
                    height: 180px;
                    text-align: center;
                    .top{
                        padding: 40px 0 10px;
                        .unit{
                            font-size: 26px;
                            display: inline-block;
                            vertical-align: top;
                        }
                        .price{
                            font-size: 50px;
                            line-height: 50px;
                        }
                        .type{
                            font-size: 16px;
                            padding-left: 5px;
                        }
                    }
                    .bottom{
                        .condtion{
                            font-size: 14px;
                            padding-bottom: 10px;
                        }
                    }
                }
                .img-box{
                    img{
                        width: 200px;
                    }
                }
            }
            .right-box{
                float: right;
                width: 60%;
                padding-left: 2%;
                p{
                    font-size: 28px;
                }
                .table{
                    font-size: 14px;
                    line-height: 40px;
                    .el-input-number{
                        width: 110px;
                    }
                    td{
                        span{
                            padding-left: 130px;
                        }
                    }
                    td:nth-of-type(2){
                        padding-left: 10px;
                    }
                }
            }
        }
        .bottoms{
            border-top: 1px solid #ccc;
            padding: 20px 0;
            text-align: right;
            .el-button{
                width: 100px;
                height: 38px;
                line-height: 38px;
                padding: 0;
                margin-right: 20px;
                margin-left: 0;
            }
            span{
                color: #999999;
                font-size: 14px;
                margin-right: 20px;
            }
        }
        .submit-box{
            position: absolute;
            width: 500px;
            height: 300px;
            top: 50%;
            left: 50%;
            transform: translate(-50%,-50%);
            .success{
                text-align: center;
                i{
                    font-size: 80px;
                    color: green;
                    line-height: 1.3;
                }
                p{
                    font-size: 16px;
                }
            }
            .fail{
                text-align: center;
                i{
                    font-size: 70px;
                    color: red;
                    line-height: 1.3;
                }
                p{
                    font-size: 16px;
                }
            }
        }
    }
</style>